<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto" persistent>
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn
        color="success"
        class="ml-5 mt-5"
        append-icon="mdi-plus"
        v-bind="activatorProps"
        >Novo Protocolo</v-btn
      >
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" width="900">
        <v-card-title class="bg-success">Novo Protocolo</v-card-title>
        <v-card-text>
          <ProtocolForm
            :protocol="protocol"
            :eoitypes="eoitypes"
            :formValid="formValid"
            @update:protocol="localProtocol = $event"
            @update:formValid="formValid = $event"
            createMode
          />
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            color="error"
            variant="tonal"
            prepend-icon="mdi-close"
            @click="dialog = false"
            >Cancelar</v-btn
          >
          <v-spacer></v-spacer>
          <v-btn
            color="success"
            variant="flat"
            append-icon="mdi-check"
            :loading="isLoading"
            :disabled="!protocolReady"
            @click="postProtocol()"
            >Adcionar</v-btn
          >
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import ProtocolForm from "./ProtocolForm.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddProtocol",
  components: {
    ProtocolForm,
  },
  props: {
    eoitypes: {
      type: Array,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return {
      accountStore,
      generalStore,
    };
  },
  data() {
    return {
      dialog: false,
      isLoading: false,
      formValid: false,
      protocol: {
        date_min: "",
        date_max: "",
        eoi: {
          name: null,
          number: null,
        },
        observations: [],
      },
    };
  },
  computed: {
    protocolReady() {
      const hasObservations = this.protocol.observations.length > 0;
      const hasIterations = this.protocol.observations.every(
        (observation) => observation.iterations.length > 0
      );
      return hasObservations && hasIterations && this.formValid;
    },
  },
  methods: {
    reset() {
      this.protocol = {
        date_min: "",
        date_max: "",
        eoi: {
          name: null,
          number: null,
        },
        observations: [],
      };
    },
    async postProtocol() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/protocols",
          method: "POST",
          data: this.protocol,
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then(() => {
          this.isLoading = false;
          this.generalStore.successSnackbar("Protocolo adicionado com sucesso");
          this.$emit("refresh");
          this.dialog = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao adicionar o protocolo");
        });
    },
  },
};
</script>

<style></style>
