<template>
  <v-form v-model="localFormValid">
    <v-card elevation="0" class="scrollable">
      <v-card-title v-if="createMode">
        <v-row>
          <v-col cols="12" class="d-flex align-center justify-space-between"
            ><v-divider></v-divider><span class="text-h6 mx-2">Protocolo</span>
            <v-divider></v-divider
          ></v-col>
          <v-col cols="6">
            <v-text-field
              v-model.trim="localProtocol.name"
              label="Nome"
              placeholder="Insira o nome do protocolo"
              prepend-inner-icon="mdi-tag-text-outline"
              :readonly="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :rules="[rules.required, rules.maxLength]"
            ></v-text-field>
          </v-col>
          <v-col cols="2">
            <v-text-field
              v-model.trim="localProtocol.code"
              label="Código"
              placeholder="Insira o código do protocolo"
              prepend-inner-icon="mdi-tag-outline"
              :readonly="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :rules="[rules.required, rules.code]"
            ></v-text-field>
          </v-col>
          <v-col cols="1" class="text-center"
            ><v-divider length="50" vertical></v-divider
          ></v-col>
          <v-col cols="3">
            <v-autocomplete
              v-model="localProtocol.enemy"
              :items="enemies"
              item-title="enemy_name"
              item-value="enemy_id"
              label="Inimigo"
              placeholder="Selecione o inimigo"
              prepend-inner-icon="mdi-bug-outline"
              :readonly="readonly"
              :hide-details="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :loading="isLoadingEnemies"
              :rules="[rules.required]"
            ></v-autocomplete>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-row>
          <!-- <v-col cols="12" class="d-flex align-center justify-space-between"
            ><v-divider></v-divider
            ><span class="text-subtitle-1 mx-2">Duração</span>
            <v-divider></v-divider
          ></v-col>
          <v-col cols="4">
            <v-select
              v-model="localProtocol.date_min"
              label="Duração min"
              placeholder="Selecione a duração mínima"
              prepend-inner-icon="mdi-clock-minus-outline"
              :items="timeSelectItems"
              item-title="title"
              item-value="value"
              :readonly="readonly"
              :hide-details="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :clearable="!readonly"
            ></v-select>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="localProtocol.date_max"
              label="Duração máx"
              placeholder="Selecione a duração máxima"
              prepend-inner-icon="mdi-clock-plus-outline"
              :items="timeSelectItems"
              item-title="title"
              item-value="value"
              :readonly="readonly"
              :hide-details="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :clearable="!readonly"
            ></v-select>
          </v-col> -->
          <v-col cols="12" class="d-flex align-center justify-space-between"
            ><v-divider></v-divider
            ><span class="text-subtitle-1 mx-2 whitespace-nowrap"
              >EOIs a estudar</span
            >
            <v-divider></v-divider
          ></v-col>
          <v-col cols="4">
            <v-select
              v-model="localProtocol.eoi.name"
              :items="eoitypes"
              item-title="eoitype_name"
              item-value="eoitype_name"
              label="Tipo de EOI"
              placeholder="Selecione o tipo de EOI"
              prepend-inner-icon="mdi-map-marker-alert-outline"
              :readonly="readonly"
              :hide-details="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :rules="[rules.required]"
            ></v-select>
          </v-col>
          <v-col cols="2">
            <v-text-field
              v-model.number="localProtocol.eoi.number"
              label="Nº eois"
              type="number"
              suffix="eois"
              :readonly="readonly"
              :hide-details="readonly"
              density="compact"
              color="primary"
              variant="outlined"
              :rules="[rules.required, rules.naturalNumber]"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-col cols="12" class="d-flex align-center justify-space-between"
          ><v-divider></v-divider
          ><span class="text-subtitle-1 mx-2 whitespace-nowrap"
            >Observações</span
          >
          <v-divider></v-divider
        ></v-col>
        <v-col
          cols="12"
          v-for="(obs, index) in localProtocol.observations"
          :key="index"
          class="d-flex align-center justify-space-between"
        >
          <v-btn
            :icon="`mdi-numeric-${index + 1}`"
            color="secondary"
            readonly
            size="small"
            variant="tonal"
          >
          </v-btn>
          <v-container class="py-0">
            <ProtocolObservationCard :observation="obs" :readonly="readonly" />
          </v-container>
          <v-btn
            v-if="editMode || createMode"
            icon="mdi-close"
            color="error"
            size="small"
            variant="tonal"
            @click="localProtocol.observations.splice(index, 1)"
          ></v-btn>
        </v-col>
        <v-col
          cols="12"
          v-if="editMode || createMode"
          class="d-flex align-center justify-space-between"
        >
          <v-btn
            icon="mdi-plus"
            color="secondary"
            readonly
            size="small"
            variant="tonal"
          ></v-btn>
          <v-container class="py-0">
            <v-btn
              color="primary"
              variant="tonal"
              block
              size="large"
              @click="pushObservation"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </v-container>
        </v-col>
      </v-card-text>
    </v-card>
  </v-form>
</template>

<script>
import ProtocolObservationCard from "./ProtocolObservationCard.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "ProtocolForm",
  components: {
    ProtocolObservationCard,
  },
  props: {
    protocol: Object,
    formValid: Boolean,
    readonly: Boolean,
    eoitypes: Array,
    editMode: Boolean,
    createMode: Boolean,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return {
      accountStore,
      generalStore,
    };
  },
  data() {
    return {
      enemies: [],
      isLoadingEnemies: false,
      rules: {
        required: (value) => !!value || "Campo obrigatório",
        maxLength: (value) =>
          value.length <= 25 || "O campo deve ter no máximo 25 caracteres",
        naturalNumber: (value) =>
          (Number.isInteger(value) && value >= 0) || "Número inválido",
        code: (value) =>
          (value && value.length <= 3 && value.toUpperCase() === value) ||
          "Código inválido",
      },
      timeSelectItems: [
        { title: "30 min", value: 30 },
        { title: "1 hora", value: 60 },
        { title: "2 horas", value: 120 },
        { title: "3 horas", value: 180 },
        { title: "4 horas", value: 240 },
        { title: "5 horas", value: 300 },
        { title: "6 horas", value: 360 },
        { title: "7 horas", value: 420 },
        { title: "8 horas", value: 480 },
        { title: "9 horas", value: 540 },
        { title: "10 horas", value: 600 },
        { title: "11 horas", value: 660 },
        { title: "12 horas", value: 720 },
        { title: "13 horas", value: 780 },
        { title: "14 horas", value: 840 },
        { title: "15 horas", value: 900 },
        { title: "16 horas", value: 960 },
        { title: "17 horas", value: 1020 },
        { title: "18 horas", value: 1080 },
        { title: "19 horas", value: 1140 },
        { title: "20 horas", value: 1200 },
        { title: "21 horas", value: 1260 },
        { title: "22 horas", value: 1320 },
        { title: "23 horas", value: 1380 },
        { title: "24 horas", value: 1440 },
      ],
    };
  },
  computed: {
    localProtocol: {
      get() {
        return this.protocol;
      },
      set(value) {
        this.$emit("update:protocol", value);
      },
    },
    localFormValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
  },
  methods: {
    pushObservation() {
      this.localProtocol.observations.push({
        name: null,
        iterations: [],
      });
    },
    async listEnemies() {
      this.isLoadingEnemies = true;
      await axios
        .request({
          url: "api/enemies",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.enemies = response.data;
          this.isLoadingEnemies = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar inimigos");
          this.isLoadingEnemies = false;
        });
    },
  },
  mounted() {
    if (this.createMode) this.listEnemies();
  },
};
</script>

<style scoped>
.whitespace-nowrap {
  white-space: nowrap;
}
.full-width {
  width: 90%;
}
.scrollable {
  overflow-y: auto;
  max-height: 700px;
}
</style>
