<template>
  <v-card>
    <v-card-title class="d-flex justify-space-between align-center">
      <v-row>
        <v-col cols="6">
          <v-text-field
            v-model="localObservation.name"
            label="Nome da Observação"
            placeholder="Insira o nome da observação"
            prepend-inner-icon="mdi-eye"
            :readonly="readonly"
            :hide-details="readonly"
            density="compact"
            color="secondary"
            variant="outlined"
            :rules="[rules.required, rules.maxLength]"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-btn variant="plain" color="primary" icon @click="show = !show">
        <v-icon>{{ show ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
        <v-tooltip activator="parent" location="right">
          <span>{{ show ? "Ocultar Etapas" : "Mostrar Etapas" }}</span>
        </v-tooltip>
      </v-btn>
    </v-card-title>
    <v-expand-transition>
      <div v-show="show">
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col
              cols="12"
              v-for="(it, index) in localObservation.iterations"
              :key="index"
              class="d-flex align-top justify-space-between"
            >
              <v-btn
                class="mr-4"
                :icon="`mdi-numeric-${index + 1}`"
                color="secondary"
                readonly
                size="small"
                variant="tonal"
              >
              </v-btn>
              <v-row align="center">
                <v-col cols="5">
                  <v-text-field
                    v-model.trim="it.name"
                    label="Nome da Etapa"
                    placeholder="Insira o nome da etapa"
                    :readonly="readonly"
                    :hide-details="readonly"
                    density="compact"
                    color="secondary"
                    variant="outlined"
                    :rules="[rules.required, rules.maxLength]"
                  ></v-text-field>
                </v-col>
                <v-col>
                  <v-select
                    cols="3"
                    v-model="it.data_type"
                    :items="[
                      { title: 'Quantitativo', value: 1 },
                      { title: 'Qualitativo', value: 0 },
                    ]"
                    label="Tipo de Dado"
                    item-title="title"
                    item-value="value"
                    :readonly="readonly"
                    :hide-details="readonly"
                    density="compact"
                    color="secondary"
                    variant="outlined"
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="3" v-if="it.data_type == 1">
                  <v-text-field
                    v-model.trim="it.units"
                    label="Unidades"
                    :readonly="readonly"
                    :hide-details="readonly"
                    density="compact"
                    color="secondary"
                    variant="outlined"
                    :rules="[rules.required, rules.maxLength]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-btn
                v-if="!readonly"
                class="mx-2"
                icon="mdi-close"
                color="error"
                size="small"
                variant="tonal"
                @click="localObservation.iterations.splice(index, 1)"
              ></v-btn>
            </v-col>
            <v-col
              cols="12"
              v-if="!readonly"
              class="d-flex align-top justify-space-between"
            >
              <v-btn
                class="mr-4"
                icon="mdi-plus"
                color="secondary"
                readonly
                size="small"
                variant="tonal"
              ></v-btn>
              <v-row align="center">
                <v-col cols="12">
                  <v-btn
                    color="primary"
                    variant="tonal"
                    block
                    size="large"
                    @click="pushIteration"
                  >
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card-text>
      </div>
    </v-expand-transition>
  </v-card>
</template>

<script>
import { readonly } from "vue";

export default {
  name: "ProtocolObservationCard",
  props: {
    observation: {
      type: Object,
      required: true,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      show: false,
      rules: {
        required: (v) => !!v || "Campo obrigatório.",
        maxLength: (v) => (v && v.length <= 25) || "Máximo de 25 caracteres",
      },
    };
  },
  computed: {
    localObservation: {
      get() {
        return this.observation;
      },
      set(value) {
        console.log(value);
        this.$emit("update:observation", value);
      },
    },
  },
  methods: {
    pushIteration() {
      this.localObservation.iterations.push({
        name: "",
        data_type: null,
        units: "",
        value_type: "integer",
        max: 5,
        min: 0,
      });
    },
  },
};
</script>

<style></style>
