<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-card class="mx-auto" width="900">
      <v-card-title class="bg-secondary">{{ localProtocol.name }}</v-card-title>
      <v-card-text>
        <ProtocolForm
          :protocol="localProtocol"
          :eoitypes="eoitypes"
          :formValid="formValid"
          @update:protocol="localProtocol = $event"
          @update:formValid="formValid = $event"
          editMode
        />
      </v-card-text>
      <v-card-actions class="d-flex justify-space-around">
        <v-btn
          color="error"
          variant="tonal"
          icon="mdi-close"
          @click="localDialog = false"
        ></v-btn>
        <v-btn
          color="secondary"
          variant="tonal"
          icon="mdi-check"
          :loading="isLoadingEdit"
          :disabled="!formValid"
          @click="editProtocol()"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import ProtocolForm from "./ProtocolForm.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "ProtocolEdit",
  components: {
    ProtocolForm,
  },
  props: {
    protocol: {
      type: Object,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
    eoitypes: {
      type: Array,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return {
      accountStore,
      generalStore,
    };
  },
  data() {
    return {
      isLoadingEdit: false,
      formValid: false,
    };
  },
  computed: {
    localProtocol: {
      get() {
        return this.protocol;
      },
      set(value) {
        this.$emit("update:protocol", value);
      },
    },
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
  },
  methods: {
    reset() {
      this.localProtocol = null;
    },
    async editProtocol() {
      console.log(this.localProtocol);
      let payload = { ...this.localProtocol };
      this.isLoadingEdit = true;
      await axios
        .request({
          url: `api/protocols/${this.localProtocol.name}`,
          method: "PATCH",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingEdit = false;
          this.generalStore.successSnackbar("Protocolo alterado com sucesso");
          this.$emit("refresh");
          this.localDialog = false;
        })
        .catch((error) => {
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao alterar protocolo");
          console.error(error);
        });
    },
  },
};
</script>

<style></style>
