<template>
  <v-container>
    <v-row class="d-flex justify-center">
      <v-col sm="12" md="10" lg="10" xl="6" class="d-flex align-center">
        <v-btn
          class="mt-4 mr-2"
          color="secondary"
          size="large"
          icon
          variant="text"
          @click="refreshProtocols"
        >
          <v-icon> mdi-refresh </v-icon>
        </v-btn>
        <v-text-field
          v-model="filter.name"
          color="secondary"
          placeholder="Pesquise o nome do Protocolo"
          density="comfortable"
          variant="solo"
          hide-details
          class="mt-5"
          :disabled="isLoading"
          prepend-inner-icon="mdi-magnify"
        ></v-text-field>
        <AddProtocol
          v-if="accountStore.hasRole(['Administrador'])"
          :eoitypes="eoiTypes"
          @refresh="refreshProtocols"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <v-row class="d-flex justify-center scrollable">
          <v-col sm="12" md="10" lg="10" xl="6">
            <v-sheet :height="sheetHeight">
              <v-progress-linear
                v-if="isLoading"
                color="primary"
                indeterminate
              ></v-progress-linear>
              <v-row v-else class="ml-1">
                <v-col
                  v-for="(protocol, index) in protocolsToShow"
                  :key="index"
                  cols="12"
                >
                  <ProtocolCard :protocol="protocol" :eoitypes="eoiTypes" />
                </v-col>
              </v-row>
            </v-sheet> </v-col
        ></v-row>
      </v-col>
    </v-row>
    <!-- <ProtocolEdit
      v-if="accountStore.hasRole(['Administrador'])"
      :protocol="protocolSelected"
      :dialog="editDialog"
      :eoitypes="eoiTypes"
      @update:dialog="editDialog = $event"
      @update:protocol="protocolSelected = $event"
      @refresh="refreshProtocols"
    /> -->
  </v-container>
</template>

<script>
import ProtocolCard from "@/components/Protocols/ProtocolCard.vue";
import ProtocolEdit from "@/components/Protocols/ProtocolEdit.vue";
import AddProtocol from "@/components/Protocols/AddProtocol.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "ProtocolsView",
  components: {
    ProtocolCard,
    ProtocolEdit,
    AddProtocol,
  },
  data() {
    return {
      protocols: [],
      isLoading: false,
      filter: {
        name: null,
      },
      eoiTypes: [],

      editDialog: false,
      protocolSelected: null,
    };
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  computed: {
    sheetHeight() {
      return window.innerHeight - 200;
    },
    protocolsToShow() {
      if (!this.filter.name) {
        return this.protocols;
      } else {
        return this.protocols.filter((protocol) =>
          protocol.name.includes(this.filter.name)
        );
      }
    },
  },
  methods: {
    async getProtocols() {
      await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          method: "GET",
          url: "api/protocols",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.protocols = response.data;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar os protocolos");
          this.isLoading = false;
        });
    },
    async getEoiTypes() {
      await axios
        .request({
          url: "api/eoitypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.eoiTypes = response.data;
        })
        .catch((error) => {
          console.error(error);
          this.isLoading = false;
        });
    },
    handleClickEdit(protocol) {
      this.protocolSelected = JSON.parse(JSON.stringify(protocol));
      this.editDialog = true;
      console.log(protocol);
      console.log(this.protocolSelected);
    },
    async refreshProtocols() {
      this.isLoading = true;
      await this.getProtocols();
      this.isLoading = false;
    },
  },
  async mounted() {
    this.isLoading = true;
    await Promise.all([this.getProtocols(), this.getEoiTypes()]);
    this.isLoading = false;
  },
};
</script>

<style scoped>
.scrollable {
  overflow-y: auto;
}
</style>
